import { postLogin } from '@/actions';
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      formInline: {
        user: '',
        password: '',
      },
      ruleInline: {
        user: [{
          required: true,
          message: '请输入用户名',
          trigger: 'blur',
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur',
        },
        {
          type: 'string',
          min: 6,
          message: '密码长度不能小于6位',
          trigger: 'blur',
        },
        ],
      },
    };
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          postLogin({
            username: this.formInline.user,
            password: this.formInline.password,
          }).then(({
            code, data: { msg }, data,
          }) => {
            if (code === 200) {
              this.$Notice.info({
                title: '提示',
                desc: msg,
              });
              Cookies.set('user', data);
              this.$router.push({ name: 'index' });
            } else {
              this.$Notice.error({
                title: '提示',
                desc: msg,
              });
            }
          });
        }
      });
    },
    goGister() {
      this.$router.push({ name: 'register' });
    },
  },
};
